body {
  background-color: black; }

.center {
  width: 50%;
  margin: auto; }

.hide {
  opacity: 0; }

.nerdFont {
  font-family: 'IBM Plex Mono', monospace; }

.juliya-red {
  color: #990066; }

.juliya-blue {
  color: #66FF99; }

.SharedText {
  max-width: 1000px;
  margin: auto;
  display: block;
  text-align: justify-all;
  font-size: 1.125em;
  color: whitesmoke;
  transition: .5s ease;
  font-family: 'IBM Plex Mono', monospace; }

a {
  color: #66FF99; }
